import React from 'react';
import { fetchNewCollectionsProducts } from '../redux/product/asyncActions';
import { Functions } from '../components/Functions';
import { Promo } from '../components/Promo';
import { Team } from '../components/Team';
import { TitleProducts } from '../components/TitleProducts';
import { Link } from 'react-router-dom';
import { HelmetComponent } from '../components/HelmetComponent';

export const Main: React.FC = () => {
    return (
        <>
            <HelmetComponent title='' description=' '/>
            <Promo/>
            <TitleProducts title=' ' asyncFunc={fetchNewCollectionsProducts} button={<Link to="/woomazing-shop/shop" className="action second"> </Link>}/>
            <Functions/>
            <Team/>
        </>
    );
};