import React from 'react';
import { NavLink } from 'react-router-dom';

export const Menu: React.FC = () => {
    return (
        <nav>
            <ul className="menu">
                <li><NavLink className='hover-underline-animation' to="/woomazing-shop"></NavLink></li>
                <li><NavLink className='hover-underline-animation' to="/woomazing-shop/shop"></NavLink></li>
                <li><NavLink className='hover-underline-animation' to="/woomazing-shop/about"> </NavLink></li>
                <li><NavLink className='hover-underline-animation' to="/woomazing-shop/contacts"></NavLink></li>
            </ul>
        </nav>
    );
};